.cart-card{
  padding: 10px 0;
  display: grid;
  grid-template-columns: 50px auto 50px;
  align-items: center;
  gap: 10px;
  border-bottom: 1px dashed #DBD7E9;
  &__icon{
    border-radius: 10px;
    background: #F2F1F6;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      max-width: 90%;
      max-height: 90%;
    }
  }
  &__title{
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__price{
    font-weight: 500;
    margin-top: 5px;
  }
  &__action{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__remove{
    svg{
      fill:#CECBDA;
    }
    &:hover{
      svg{
        fill: $black;
      }
    }
  }
  &__count{
    margin-right: 10px;
    font-weight: 600;
  }
}

.product-card{
  background: #FFFFFF;
  box-shadow: 0px 5px 20px #F2F1F6;
  border-radius: 20px;
  padding: 0 25px 25px;
  position: relative;
  display: flex;
  flex-direction: column;
  transition: shadow .3s, transform 1.6s,opacity 1.6s;
  &:hover{
    box-shadow: 0px 5px 20px #D5D0E7;
  }
  &__top{
    display: block;
    position: relative;
    z-index: 2;
    padding-bottom: 62.51%;
    margin-top: -20px;
    color: #fff;
  }
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    padding: 30px;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
  }
  &__icon{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: 23px;
    flex-shrink: 0;
    min-width: 50px;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc{
    font-size: 26px;
    line-height: 150%;
    letter-spacing: 0.01em;
  }
  &__text{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    padding: 10px 20px;
    font-size: 13px;
    line-height: 130%;
    letter-spacing: 0.01em;
    color: #B6C0EF;
  }
  &__badge{
    text-transform: uppercase;
    position: absolute;
    left: 37px;
    top: 0;
    transform: translateY(-50%);
    height: 25px;
    padding: 0 15px;
    display: flex;
    align-items: center;
    border-radius: 50px;
    font-weight: 600;
    font-size: 10px;
    line-height: 150%;
    letter-spacing: 0.01em;
  }
  &__title{
    display: block;
    font-size: 20px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 0.01em;
    margin: 20px auto 0;
    max-width: 230px;
  }
  &__prices{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0 10px;
  }
  &__price1{
    font-size: 16px;
    line-height: 23px;
    letter-spacing: 0.01em;
    text-decoration-line: line-through;
    margin-right: 12px;
    color: #7B739E;
  }
  &__price2{
    font-weight: 500;
    font-size: 18px;
    line-height: 26px;
    letter-spacing: 0.01em;
  }
  &__btn{
    width: 139px;
    margin: auto auto 0;
    svg{
      margin-right: 10px;
    }
  }
}

.gift-card{
  display: block;
  padding: 0 15px 10px;
  background: #FFFFFF;
  border-radius: 10px;
  &__top{
    margin-top: -20px;
    padding-bottom: 55.6%;
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    background: #F2F1F6;
    &:hover{
      color: #fff;
      .gift-card__bg{
        opacity: 1;
      }
      svg{
        fill: #fff;
      }
    }
  }
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(80.25% 171.06% at 32.76% 27.25%, #0B3A71 0%, #2568B7 100%);
    background-size: 110% 110%;
    transition: all .3s;
    opacity: 0;
    border-radius: 10px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.01em;
    svg{
      width: 40px;
      height: auto;
      margin-right: 10px;
    }
  }
  &__price{
    text-align: center;
    font-weight: 600;
    margin-top: 10px;
  }
}

.feedback-card{
  &:not(:last-child){
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #F2F1F6;
  }
  &__name{
    font-weight: 500;
    color: $gray;
  }
  &__date{
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 20px;
    color: $gray;
    margin-top: 5px;
    svg{
      margin-right: 10px;
      fill:$gray;
    }
  }
  &__text{
    @include fontWeight(350,$font2);
    margin-top: 8px;
  }
  &__answer{
    padding: 15px 30px;
    background: #F6F6FA;
    border-radius: 20px;
    width: calc(100% - 70px);
    margin: 10px 0 0 auto;
  }
  &__admin{
    font-weight: 500;
    display: flex;
    align-items: center;
    svg{
      margin-right: 10px;
    }
  }
}

.faq-card{
  padding: 20px 30px;
  border: 1px solid #F2F1F6;
  border-radius: 20px;
  cursor: pointer;
  &._active{
    .faq-card__show{
      svg{
        transform: rotate(180deg);
      }
    }
  }
  &:not(:last-child){
    margin-bottom: 10px;
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 400;
    svg{
      margin-left: 10px;
      transition: all .4s;
    }
  }
  &__hidden{
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px dashed #DBD7E9;
    display: none;
  }
}

.cart-card2{
  padding-left: 20px;
  display: grid;
  grid-template-columns: 100px auto 100px 162px 110px 100px;
  align-items: center;
  background: #FFFFFF;
  box-shadow: 0 5px 40px #F2F1F6;
  border-radius: 20px;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    left: 0;
    top: 50%;
    transform: translate(-50%,-50%);
    background: #f6f6fa;
  }
  &__img{
    background: #F2F1F6;
    border-radius: 20px;
    height: 100%;
    min-height: 100px;
    width: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(-20px);
    svg{
      max-width: 46px;
      max-height: 46px;
    }
  }
  &__title{
    padding: 0 20px;
  }
  &__price1,
  &__price2{
    &:after{
      content: attr(data-currency);
    }
  }
  &__price2{
    font-weight: 600;
  }
  &__remove{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    border-left: 1px dashed #DBD7E9;
    svg{
      fill:#CECBDA;
    }
    &:hover{
      svg{
        fill: $red;
      }
    }
  }
  &__calc{
    width: 112px;
  }
  &__subtitle{
    display: none;
  }
}

.order-card{
  background: #FFFFFF;
  box-shadow: 0px 5px 40px #F2F1F6;
  border-radius: 20px;
  position: relative;
  &__num{
    position: absolute;
    top: 0;
    left: 20px;
    height: 44px;
    border-radius: 50px;
    display: flex;
    align-items: center;
    background-color: $primary;
    transform: translateY(-50%);
    color: #FFFFFF;
    font-size: 16px;
    white-space: nowrap;
    padding: 0 30px;
  }
  &__items{
    padding: 44px 30px 10px;
  }
  &__item{
    padding: 10px 0;
    display: flex;
    align-items: center;
    &:not(:last-child){
      border-bottom: 1px solid #F2F1F6;
    }
  }
  &__img{
    width: 60px;
    height: 60px;
    margin-right: 20px;
    flex-shrink: 0;
    background: #F2F1F6;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    svg{
      max-width: 26px;
      max-height: 100%;
    }
  }
  &__price{
    margin-left: auto;
    font-weight: 500;
    white-space: nowrap;
    padding-left: 10px;
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    border-top: 1px dashed #DBD7E9;
  }
  &__total{
    border-radius: 0px 0px 20px 0px;
    background-color:$primary;
    height: 90px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 222px;
    color: #fff;
    margin-left: 28px;
  }
  &__btn{
    padding: 0 24.5px;
    margin-right: 20px;
    svg{
      margin-left: 10px;
      fill: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .cart-card2{
    grid-template-columns: 100px auto 80px 132px 90px 100px;
  }
}


@media screen and (max-width: 768px){
  .product-card{
    &__content{
      padding-left: 20px;
      padding-right: 20px;
    }
    &__icon{
      max-width: 60px;
      height: 70px;
      margin-right: 17px;
    }
    &__name{
      font-size: 12.0376px;
    }
    &__desc{
      font-size: 19.5611px;
    }
    &__badge{
      left: 16px;
    }
    &__title,
    &__prices{
      margin-top: 10px;
    }
    &__text{
      padding: 7px 15px;
      font-size: 9.78056px;
    }
  }

  .feedback-card{
    &__answer{
      padding-left: 20px;
      padding-right: 20px;
      width: 100%;
    }
  }

  .faq-card{
    padding-left: 20px;
    padding-right: 20px;
  }

  .cart-card2{
    grid-template-columns: 100%;
    gap: 20px;
    padding: 20px;
    &__img{
      transform: unset;
      margin: 0 auto;
      width: 100px;
      height: 100px;
    }
    &__title{
      text-align: center;
      font-weight: 600;
      font-size: 20px;
    }
    &__price1,
    &__price2{
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 10px;
      font-size: 18px;
    }
    &__subtitle{
      display: block;
      width: 100px;
    }
    &__calc{
      margin: 0 auto;
    }
    &__remove{
      position: absolute;
      width: unset;
      height: unset;
      top: 20px;
      right: 20px;
      border: none;
    }
  }

  .order-card{
    border-radius: 20px;
    &__items{
      padding-left: 15px;
      padding-right: 15px;
    }
    &__item{
      flex-direction: column;
      text-align: center;
    }
    &__img{
      margin: 0 0 15px;
    }
    &__price{
      margin: 10px 0 0 0;
    }
    &__nav{
      padding: 15px 15px 0;
      display: grid;
      grid-template-columns: 100%;
      gap: 10px;
    }
    &__total{
      width: calc(100% + 30px);
      margin: 20px 0 0 -15px;
      border-radius: 0 0 20px 20px;
      height: 65px;
    }
    &__btn{
      margin-right: 0;
    }
  }
}
