.fg {
  position: relative;
  margin: 0;
  input {
    background: #F2F1F6;
    border: 1px solid #DBD7E9;
    border-radius: 10px;
    padding: 0 10px;
    height: 50px;
    width: 100%;
    font-size: 16px;
    line-height: 150%;
    @include fontWeight(350,$font2);
    &::placeholder {
      color: $gray;
    }
    &:focus{
      background-color: transparent;
      border-color: $primary;
    }
  }
  &__icon{
    position: absolute;
    left: 20px;
    bottom: 50%;
    transform: translateY(50%);
    fill:$gray;
    pointer-events: none;
    &~input{
      padding-left: 44px;
    }
  }
  &--white{
    input{
      background-color: #fff;
    }
  }
}

.fg-pass{
  &__btn{
    position: absolute;
    bottom: 50%;
    transform: translateY(50%);
    right: 20px;
    width: 12px;
    height: 12px;
    background-image: url(../img/eye.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% auto;
    &._show-pass{
      background-image: url(../img/eye2.svg);
    }
  }
  input{
    padding-right: 44px;
  }
}

.fg-label{
  label{
    position: absolute;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.01em;
    color: $gray;
    left: 20px;
    top: 50%;
    transform: translateY(-50%);
    transition: all .3s;
    pointer-events: none;
  }
  input{
    transition: all .3s;
    height: 60px;
    padding: 0 20px;
    &.input-filled,
    &:focus{
      padding-top: 20.5px;
      &+label{
        transform: translateY(0%);
        top: 9.5px;
        font-size: 12px;
      }
    }
  }
  .fg-pass__btn{
    width: 18px;
    height: 18px;
  }
  .fg__icon{
    &~label{
      left: 48px;
    }
    &~input{
      padding-left: 48px;
    }
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        border-color: $primary;
        background-image: url(../img/check.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: 85% auto;
      }
    }
  }
  label {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 24px;
    font-size: 12px;
    line-height: 150%;
    letter-spacing: 0.01em;
    @include fontWeight(350,$font2);
    cursor: pointer;
    &:before {
      content: '';
      z-index: 1;
      border: 1px solid #DBD7E9;
      border-radius: 10px;
      width: 24px;
      height: 24px;
      flex-shrink: 0;
      margin-right: 10px;
      display: block;
    }
  }
  a{
    position: relative;
    z-index: 2;
    color: $primary;
    &:hover{
      text-decoration: underline !important;
    }
  }
  &--white{
    label{
      &:before{
        background-color: #fff;
      }
    }
  }
}

.cartcalc{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  background: #F2F1F6;
  border-radius: 10px;
  padding: 0 20px;
  height: 50px;
  input{
    border: none;
    background-color: transparent;
    width: 32px;
    text-align: center;
    font-size: 16px;
    height: 20px;
  }
  button{
    height: 20px;
  }
}
