.contacts{
  padding-bottom: 50px;
  &__desc{
    margin-top: 20px;
    font-size: 20px;
    line-height: 150%;
    letter-spacing: 0.01em;
  }
  &__items{
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 30px;
  }
  &__item{
    padding: 10px 0 0 30px;
    position: relative;
  }
  &__icon{
    border-radius: 20px;
    position: absolute;
    background-color: $primary;
    width: 80px;
    height: 80px;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__info{
    background: #FFFFFF;
    box-shadow: 0px 5px 40px #F2F1F6;
    border-radius: 20px;
    min-height: 120px;
    height: 100%;
    padding: 32px 30px 32px 61px;
  }
  &__subtitle{
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.01em;
    color: $gray;
    margin-bottom: 5px;
  }
  &__link{
    font-size: 20px;
    line-height: 150%;
    letter-spacing: 0.01em;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    &__items{
      grid-template-columns: 1fr 1fr;
    }
  }
}


@media screen and (max-width: 768px){
  .contacts{
    &__desc{
      font-size: 18px;
      line-height: 130%;
    }
    &__items{
      grid-template-columns: 1fr;
    }
  }
}
