.footer{
  padding: 30px 0;
  max-width: calc(100% - 42px);
  width: 100%;
  margin: 0 auto;
  background: #F2F1F6;
  border-radius: 30px 30px 0px 0px;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__socials{
    display: flex;
    align-items: center;
  }
  &__social{
    &:not(:last-child){
      margin-right: 10px;
    }
  }
  &__c{
    margin-top: 32px;
    padding-top: 30px;
    border-top: 1px solid #D3CFE2;
    text-align: center;
    font-size: 14px;
    line-height: 20px;
    color: #7D769A;
  }
}

.footer-nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 18px;
    line-height: 26px;
    &:not(:last-child){
      margin-right: 40px;
    }
  }
}


@media screen and (max-width: 1150px){
  .footer{
    &__inner{
      flex-direction: column;
    }
    &__socials{
      margin-top: 20px;
    }
    &__c{
      margin-top: 20px;
      padding-top: 20px;
    }
  }

  .footer-nav{
    margin-top: 20px;
  }
}


@media screen and (max-width: 992px){
  .footer{
    max-width: calc(100% - 30px);
    border-radius: 20px 20px 0px 0px;
    &__socials{
      padding-top: 20px;
      border-top: 1px solid #d3cfe2;
      width: 100%;
      justify-content: center;
    }
  }

  .footer-nav{
    flex-direction: column;
    &__link{
      text-align: center;
      &:not(:last-child){
        margin: 0 0 10px;
      }
    }
  }
}
