@import '../_mixins';
@include font-face('Jost', 'Jost-Regular', 400);
@include font-face('Jost', 'Jost-Medium', 500);
@include font-face('Jost', 'Jost-Semibold', 600);
@include font-face('Burbank Big Condensed', 'burbankbigcondensed_bold', 700);
@font-face {
  font-family: 'JostVariable';
  font-style: normal;
  font-display: swap;
  font-weight: 100 900;
  src: url('../fonts/Jost.woff') format('woff'),
      url('../fonts/Jost.woff2') format('woff2');
  unicode-range: U+0301,U+0400-045F,U+0490-0491,U+04B0-04B1,U+2116;
}

@font-face {
  font-family: 'JostVariable Cyrillic';
  font-style: normal;
  font-display: swap;
  font-weight: 100 900;
  src: url('../fonts/jost-cyrillic-variable-wghtOnly-normal.woff2') format('woff2');
  unicode-range: U+0301,U+0400-045F,U+0490-0491,U+04B0-04B1,U+2116;
}
/* jost-latin-variable-wghtOnly-normal */
@font-face {
  font-family: 'JostVariable Latin';
  font-style: normal;
  font-display: swap;
  font-weight: 100 900;
  src: url('../fonts/jost-latin-variable-wghtOnly-normal.woff2') format('woff2');
  unicode-range: U+0000-00FF,U+0131,U+0152-0153,U+02BB-02BC,U+02C6,U+02DA,U+02DC,U+2000-206F,U+2074,U+20AC,U+2122,U+2191,U+2193,U+2212,U+2215,U+FEFF,U+FFFD;
}
