@import '../vars';
.modal {
    background: rgba($black, .9);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    opacity: 0;
    pointer-events: none;
    &--active{
        opacity: 1;
        pointer-events: unset;
    }
    &__dialog{
        background-color: #fff;
        border-radius: 30px;
        width: 510px;
        max-width: calc(100% - 20px);
        overflow-y: auto;
        max-height: 100vh;
        padding: 30px;
        position: relative;
    }
    &__title{
        text-align: center;
    }
    &__product{
        display: flex;
        align-items: center;
        margin-top: 20px;
        &-icon{
            background: #F2F1F6;
            border-radius: 10px;
            margin-right: 14px;
            flex-shrink: 0;
            width: 50px;
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
            svg{
                max-width: 20px;
                max-height: 100%;
            }
        }
        &-title{
            flex-grow: 1;
            margin-right: 10px;
        }
        &-price{
            margin-left: auto;
            flex-shrink: 0;
            white-space: nowrap;
            font-weight: 500;
            min-width: 81px;
        }
    }
    &__nav{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 10px;
        margin-top: 20px;
    }
    &__btn{
        font-size: 14px;
        svg{
            margin-right: 10px;
        }
    }
}



@media screen and (max-width: 576px){
    .modal{
        &__dialog{
            padding: 20px;
            border-radius: 20px;
        }
        &__product{
            display: grid;
            grid-template-columns: 100%;
            gap: 10px;
            text-align: center;
            &-icon{
                margin: 0 auto;
            }
            &-price{
                min-width: unset;
                margin: 0;
            }
        }
        &__nav{
            grid-template-columns: 100%;
        }
    }
}
