.profile{
  padding-bottom: 50px;
  &__form{
    margin-top: 20px;
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
  }
  &__group{
    background: #FFFFFF;
    box-shadow: 0px 5px 40px #F2F1F6;
    border-radius: 20px;
    padding: 30px;
  }
  &__grid{
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 30px;
  }
  &__btn{
    margin: 20px 0 0 auto;
    width: 250px;
  }
}


@media screen and (max-width: 992px){
  .profile{
    &__grid{
      grid-template-columns: 1fr 1fr;
    }
  }
}


@media screen and (max-width: 768px){
  .profile{
    &__group{
      padding: 20px;
      border-radius: 20px;
    }
    &__grid{
      gap: 15px;
      grid-template-columns: 100%;
    }
    &__btn{
      width: 100%;
    }
  }
}
