@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";

[data-currency="â‚½"]{
  &:after{
    content: attr(data-currency);
  }
}

._animate{
  opacity: 0;
  transition: all 1.6s ease;
  transform: translateY(80px);
  transition-delay: .3s;
  &.opacity-only{
    transform: unset;
  }
  &._animated{
    opacity: 1;
    transform: translateY(0px);
  }
}

.dashed-bg{
  &:before{
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    border: 1px dashed #E4E1EF;
    border-radius: inherit;
    transform: translate(-50%,-50%);
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    pointer-events: none;
  }
}

.sprite-black{
  fill: $black;
}

._active{
  .m-dropdown{
    &__triangle-wrap{
      opacity: 1;
      transform: translate(-50%,-90%);
    }
  }
}

.m-dropdown{
  position: absolute;
  left: 50%;
  top: 100%;
  transform: translate(-50%,6px);
  box-shadow: 0px 5px 40px #F2F1F6;
  transition: all .5s;
  opacity: 0;
  pointer-events: none;
  &__inner{
    background: #FFFFFF;
    position: relative;
    z-index: 2;
    padding: 20px;
    transition: inherit;
    transform: translateY(50px);
  }
  &__triangle{
    transition: inherit;
    position: absolute;
    background-color: #fff;
    text-align: left;
    width:  7.5px;
    height: 7.5px;
    border-top-right-radius: 40%;
    transform: rotate(-60deg) skewX(-30deg) scale(1,.866);
    &-wrap{
      opacity: 0;
      position: absolute;
      top: -5px;
      left: 50%;
      transform: translate(-50%,50px);
    }
    &:before,
    &:after {
      content: '';
      position: absolute;
      background-color: inherit;
      width:  inherit;
      height: inherit;
      border-top-right-radius: 40%;
    }
    &:before{
      transform: rotate(-135deg) skewX(-45deg) scale(1.414,.707) translate(0,-50%);
    }
    &:after{
      transform: rotate(135deg) skewY(-45deg) scale(.707,1.414) translate(50%);
    }
  }
}

.cut-section{
  border-radius: 30px;
  max-width: calc(100% - 42px);
  width: 100%;
  margin: 0 auto;
}


@media screen and (max-width: 992px){
  .cut-section{
    max-width: calc(100% - 30px);
    border-radius: 20px;
  }
}
