@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary,
.m-btn-primary-outline{
    border-radius: 10px;
    height: 50px;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.01em;
    font-weight: 500;
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    svg{
        fill:#fff;
    }
    &:hover{
        color: #fff;
        background: #212AC8;
    }
    &:active{
        color: #fff;
        background: #5C64EF;
    }
    &:disabled{
        color: #A8A2BE;
        background: #F2F1F6;
        pointer-events: none;
        svg{
            fill:#A8A2BE;
        }
    }
}


.m-btn-primary-outline{
    border: 1px solid $primary;
    color: $black;
    svg{
        fill:$black;
    }
    &:hover{
        color: #fff;
        background: #212AC8;
        border-color: #212AC8;
        svg{
            fill: #fff;
        }
    }
    &:active{
        color: #fff;
        background: #5C64EF;
        border-color: #5C64EF;
        svg{
            fill: #fff;
        }
    }
    &:disabled{
        border-color: $gray;
        color: $gray;
        pointer-events: none;
    }
}
