@import "vars";
@mixin font-face($name, $file, $weight: 400, $style: normal) {
    @font-face {
        font-family: "#{$name}";
        src: url('../fonts/#{$file}.ttf') format('ttf'),
        url('../fonts/#{$file}.woff2') format('woff2'),
        url('../fonts/#{$file}.woff') format('woff'),
        url('../fonts/#{$file}.eot') format('eot');
        font-weight: $weight;
        font-style: $style;
        font-display: swap;
    }
}

@mixin fontWeight($weight,$font){
    font-weight: $weight;
    font-family: $font;
    font-variation-settings: "wght" $weight;
}

@mixin scroll {
    &::-webkit-scrollbar {
        width: 4px;
        height: 4px;
        border-radius: 50px;
    }
    &::-webkit-scrollbar-track {
        background: #F2F1F6;
        border-radius: 50px;
        width: 4px;
    }
    &::-webkit-scrollbar-thumb {
        background: $primary;
        border-radius: 50px;
    }
}
