.display1,
.display2,
.display3{
  font-weight: 500;
  line-height: 150%;
  letter-spacing: 0.01em;
}

.display1{
  font-size: 40px;
}

.display2{
  font-size: 24px;
}

.display3{
  font-size: 20px;
}

.body-text{
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.01em;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 30px;
    line-height: 125%;
  }
}

@media screen and (max-width: 576px){
  .display1{
    font-size: 25px;
  }

  .display2{
    font-size: 22px;
    line-height: 125%;
  }

  .display3{
    font-size: 18px;
  }
}
