@import '../_mixins.scss';

.main{
  max-width: calc(100% - 42px);
  width: 100%;
  margin: 0 auto;
  background-image: url(../img/main-bg.webp);
  background-size: cover;
  background-position: center;
  border-radius: 30px;
  &__inner{
    position: relative;
    padding-left: 120px;
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  &__img{
    padding-top: 40px;
    width: calc(100% + 35px);
    margin-left: -35px;
    transition: all 1s;
    transition-delay: 1.2s;
    opacity: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__translate{
    opacity: 0;
    transform: translateY(70px);
    transition-delay: .2s;
    transition: all .7s;
  }
  &__badge{
    padding: 0 15px;
    height: 25px;
    display: inline-flex;
    align-items: center;
    background: #C61FD0;
    border-radius: 50px;
    letter-spacing: 0.1em;
    color: #FFFFFF;
    font-weight: 600;
    font-size: 10px;
    white-space: nowrap;
  }
  &__logo{
    transition-delay: .4s;
    width: 100%;
    display: block;
    margin-top: 20px;
  }
  &__subtitle{
    font-family: $font3;
    font-weight: 700;
    font-size: 46px;
    margin-top: 20px;
    color: #FFFFFF;
    transition-delay: .6s;
  }
  &__nav{
    display: flex;
    align-items: center;
    margin-top: 30px;
    background: #FFFFFF;
    border-radius: 10px;
    height: 50px;
    max-width: 291px;
    transition-delay: .8s;
  }
  &__price{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    flex-grow: 1;
    &-old{
      font-size: 16px;
      line-height: 23px;
      letter-spacing: 0.01em;
      text-decoration-line: line-through;
      color: #7B739E;
      margin-right: 12px;
    }
    &-new{
      font-weight: 500;
      font-size: 18px;
      line-height: 26px;
      letter-spacing: 0.01em;
    }
  }
  &__btn{
    width: 139px;
    border-radius: inherit;
    svg{
      fill:#fff;
      margin-right: 10px;
    }
  }

  .swiper{
    &-button{
      background: rgba(68, 64, 97, 0.5);
      backdrop-filter: blur(1px);
      border-radius: 10px;
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      &:hover{
        background: rgba(68, 64, 97, 0.8);
      }
      &-prev{
        left: 20px;
      }
      &-next{
        right: 20px;
      }
    }
    &-slide{
      display: grid;
      grid-template-columns: 323px calc(100% - 323px);
      align-items: center;
      &-active{
        .main__img{
          opacity: 1;
        }
        .main__translate{
          opacity: 1;
          transform: translateY(0);
        }
      }
    }
    &-pagination{
      display: none;
    }
  }
}

.catalog{
  padding: 50px 0;
  &__grid{
    margin-top: 30px;
    padding-top: 63px;
    border-top: 1px solid #F2F1F6;
    display: grid;
    grid-template-columns: repeat(3,calc(33.33% - 20px));
    gap: 63px 30px;
  }
  &__more{
    width: 260px;
    height: 60px;
    margin: 20px auto 0;
    svg{
      margin-left: 10px;
      fill: $primary;
    }
  }
}

.brand-filter{
  display: flex;
  align-items: center;
  padding: 5px 2px;
  background: #F2F1F6;
  border-radius: 10px;
  position: relative;
  z-index: 3;
  &__title{
    padding: 0 30px;
    white-space: nowrap;
    font-weight: 500;
  }
  &__items{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    height: 50px;
    overflow: hidden;
  }
  &__item{
    position: relative;
    margin-right: 2px;
    flex-shrink: 0;
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;
      &:checked,
      &:hover{
        &+label{
          background-color: $primary;
          color: #fff;
          svg,
          path{
            fill: #fff;
          }
          .path-transparent{
            fill: $primary;
          }
          .path-light{
            fill: rgba(#fff,.7);
          }
          .path-light2{
            fill: rgba(#fff,.4);
          }
        }
      }
      &:hover{
        &+label{
          background: $black;
          .path-transparent{
            fill: $black;
          }
        }
      }
    }
    label{
      white-space: nowrap;
      border-radius: 10px;
      height: 50px;
      padding: 0 30px;
      display: flex;
      align-items: center;
      transition: all .3s;
      svg{
        margin-right: 10px;
        width: 16px;
        height: auto;
        transition: inherit;
      }
    }
  }
  &__btn{
    margin-left: auto;
    padding: 0 30px;
    flex-shrink: 0;
    font-weight: 400;
    svg{
      fill: #fff;
      margin-left: 10px;
    }
  }
  &__btn2{
    display: none;
  }
  &._active{
    .all-brands{
      opacity: 1;
      pointer-events: unset;
    }
    .brand-filter__btn,
    .brand-filter__btn2{
      pointer-events: none;
    }
    .m-dropdown{
      &__inner{
        transform: translateY(0);
      }
      &__triangle-wrap{
        transform: translate(0%,-90%);
      }
    }
  }
}

.all-brands{
  border-radius: 20px;
  position: absolute;
  width: 280px;
  left: unset;
  right: 0;
  box-shadow: 0px 5px 40px rgba(242, 241, 246, 0.5);
  transform: translateY(-10px);
  .m-dropdown{
    &__inner{
      padding-right: 10px;
      border-radius: 20px;
    }
    &__triangle{
      &-wrap{
        left: unset;
        right: 45px;
      }
    }
  }
  &__title,
  &__bg,
  &__close{
    display: none;
  }
  &__items{
    padding-right: 10px;
    max-height: 270px;
    overflow-y: auto;
    @media screen and (min-width: 768px){
      @include scroll;
    }
  }
  .brand-filter__item{
    margin-right: 0;
    &:not(:last-child){
      margin-bottom: 5px;
    }
  }
}

.props-filter{
  padding: 0 30px;
  margin-top: 20px;
  &__subtitle,
  &__bg,
  &__close{
    display: none;
  }
  &__wrap{
    display: flex;
  }
  &__title{
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.01em;
    margin-bottom: 10px;
  }
  &__group{
    &:not(:last-child){
      margin-right: 36px;
    }
  }
  &__range{
    flex-grow: 1;
    max-width: 316px;
    padding-bottom: 28px;
    .props-filter__title{
      margin-bottom: 16px;
    }
    .range-slider{
      width: calc(100% - 8px);
      margin: 0 auto;
    }
    .noUi-target{
      height: 4px;
      background: #DBD7E9;
      border-radius: 50px;
      box-shadow: none;
      border: none;
    }
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      width: 4px;
      height: 16px;
      background-color: $primary !important;
      right: 0px;
      box-shadow: none;
      border: none;
      &:after{
        background-color: transparent;
        width: 30px;
        height: 30px;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
      }
      &:before{
        content: attr(data-output);
        display: flex;
        background-color: transparent;
        font-size: 14px;
        line-height: 20px;
        height: 20px;
        width: unset;
        top: 100%;
        margin-top: 2px;
      }
      &-upper{
        &:before{
          right: 0;
          left: unset;
        }
      }
      &-lower{
        &:before{
          left: 0;
        }
      }
    }
  }
  &__nav{
    padding: 10px 0;
    display: flex;
    align-items: center;
  }
  &__apply{
    flex-shrink: 0;
    padding: 0 30px;
  }
  &__reset{
    margin-left: 35px;
    svg{
      fill: #CECBDA;
    }
    span{
      display: none;
    }
    &:hover{
      svg{
        fill: $red;
      }
    }
  }
  &__toggle{
    display: none;
  }
}

.country-select{
  position: relative;
  &__toggle,
  &__item{
    font-size: 16px;
    line-height: 150%;
    @include fontWeight(350,$font2);
    display: flex;
    align-items: center;
    img{
      flex-shrink: 0;
      margin-right: 10px;
      width: 20px;
      display: block;
    }
    svg{
      margin-left: 10px;
    }
    &:hover{
      color: $primary;
      cursor: pointer;
    }
  }
  &__toggle{
    position: relative;
    z-index: 3;
  }
  &__dropdown{
    position: absolute;
    z-index: 2;
    background: #FFFFFF;
    box-shadow: 0px 5px 40px #F2F1F6;
    border-radius: 10px;
    width: calc(100% + 40px);
    padding-top: 34px;
    top: -5px;
    left: 50%;
    transform: translateX(-50%);
    transition: all .3s;
    pointer-events: none;
    opacity: 0;
  }
  &__items{
    padding: 5px 20px;
    border-top: 1px solid #F2F1F6;
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
  }
  &._active{
    .country-select__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
    .country-select__toggle{
      svg{
        transform: rotate(180deg);
      }
    }
  }
}


@media screen and (max-width: 1170px){
  .catalog{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 15px));
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    max-width: calc(100% - 30px);
    .container{
      padding: 0 20px;
    }
    &__inner{
      padding: 0;
    }
    &__img{
      width: 100%;
      margin: 0;
      padding-top: 20px;
    }
    &__content{
      padding: 40px 0;
    }
    &__nav{
      display: block;
      height: unset;
      background-color: transparent;
    }
    &__price{
      height: 50px;
      background-color: #fff;
      border-radius: 10px;
      width: 100%;
    }
    &__btn{
      border-radius: 10px;
      margin-top: 5px;
      width: 100%;
    }
    .swiper{
      &-button{
        display: none;
      }
      &-slide{
        align-items: flex-end;
        grid-template-columns: 250px calc(100% - 250px);
      }
      &-pagination{
        z-index: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        bottom: 20px;
        left: 0;
        &-bullet{
          width: 15px;
          height: 5px;
          background: #DBD7E9;
          border-radius: 500px;
          margin: 0 5px !important;
          opacity: 1;
          &-active{
            width: 30px;
            background-color: $primary;
          }
        }
      }
    }
  }

  .brand-filter{
    &__title,
    &__btn,
    &__item label{
      padding: 0 15px;
    }
  }

  .props-filter{
    padding: 0 15px;
    &__apply{
      padding: 0 15px;
    }
    &__reset{
      margin-left: 15px;
    }
  }
}


@media screen and (max-width: 768px){
  .main{
    &__inner{
      padding: 40px 0 60px;
    }
    &__content{
      padding: 0;
    }
    &__img{
      display: none;
    }
    &__logo{
      max-width: 300px;
      width: calc(100% - 18px);
    }
    &__subtitle{
      font-size: 36px;
      margin-top: 10px;
    }
    &__nav{
      margin-top: 20px;
      max-width: 100%;
    }
    .swiper{
      &-slide{
        grid-template-columns: 100%;
      }
    }
  }

  .catalog{
    padding-top: 20px;
    &__grid{
      margin-top: 20px;
      padding-top: 33px;
      border: none;
      grid-template-columns: 100%;
      gap: 43px;
    }
  }

  .filter{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
  }

  .brand-filter{
    padding: 0;
    background-color: transparent;
    &__title,
    &__items,
    &__btn{
      display: none;
    }
    &__btn2{
      display: flex;
      justify-content: space-between;
      width: 100%;
      padding: 0 20px;
    }
    &._active{
      .m-dropdown{
        &__inner{
          transform: translateY(0%);
        }
      }
      .all-brands__bg{
        display: block;
      }
    }
    &__item{
      label{
        background: #F2F1F6;
        justify-content: center;
      }
    }
  }

  .all-brands{
    border-radius: 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    transform: unset;
    .m-dropdown{
      &__inner{
        padding: 45px 15px 0;
        transform: translateY(100%);
        position: absolute;
        top: unset;
        bottom: 0;
        right: 0;
        width: 100vw;
        height: calc(100vh - 140px);
        border-radius: 30px 30px 0px 0px;
      }
      &__triangle{
        &-wrap{
          display: none;
        }
      }
    }
    &__title{
      font-size: 20px;
      line-height: 29px;
      display: block;
    }
    &__items{
      height: calc(100% - 49px);
      max-height: calc(100% - 49px);
      min-height: 0;
      margin: 20px 0 0 -15px;
      width: calc(100% + 30px);
      padding: 0 15px;
    }

  }

  .all-brands,
  .props-filter{
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(68, 64, 97, 0.7);
    }
    &__close{
      background: $black;
      border-radius: 500px;
      position: absolute;
      top: 20px;
      left: 50%;
      transform: translateX(-50%);
      width: 80px;
      height: 5px;
      display: block;
    }
  }

  .props-filter{
    position: fixed;
    z-index: 10;
    transform: translateY(100%);
    bottom: 0;
    right: 0;
    width: 100%;
    height: calc(100vh - 140px);
    background: #FFFFFF;
    border-radius: 30px 30px 0px 0px;
    padding: 45px 15px 0;
    transition: .5s;
    &__subtitle{
      font-size: 20px;
      line-height: 29px;
      display: block;
    }
    &__wrap{
      margin: 20px 0 0 -15px;
      width: calc(100% + 30px);
      padding: 0 15px;
      height: calc(100% - 49px);
      max-height: calc(100% - 49px);
      min-height: 0;
      overflow-y: auto;
      display: block;
    }
    &__toggle{
      display: flex;
      justify-content: unset;
      padding: 0 20px;
      &._active{
        &~.props-filter{
          transform: translateY(0%);
        }
        &~.props-filter__bg{
          display: block;
        }
      }
      svg{
        margin-left: auto;
      }
      &-num{
        display: flex;
        align-items: center;
        padding: 0 10px;
        border-radius: 50px;
        height: 20px;
        background-color: $primary;
        color: #fff;
        font-weight: 600;
        font-size: 12px;
        letter-spacing: 0.01em;
        margin-left: 3px;
      }
    }
    &__group{
      max-width: unset;
      &:not(:last-child){
        margin: 0 0 20px;
      }
    }
    &__nav{
      display: grid;
      grid-template-columns: 100%;
      gap: 5px;
    }
    &__reset{
      width: 100%;
      height: 50px;
      margin: 0;
      border: 1px solid #DBD7E9;
      border-radius: 10px;
      font-weight: 500;
      font-size: 14px;
      line-height: 150%;
      letter-spacing: 0.01em;
      span{
        display: unset;
      }
      svg{
        width: 14px;
        height: 14px;
        margin-right: 10px;
      }
      &:hover{
        border-color: $primary;
        background-color: $primary;
        color: #fff;
        svg{
          fill: #fff;
        }
      }
    }
    &__bg{
      z-index: 9;
    }
  }

  .country-select{
    &__toggle{
      display: none;
    }
    &__dropdown{
      position: static;
      pointer-events: unset;
      opacity: 1;
      background: transparent;
      transform: unset;
      padding: 0;
      box-shadow: none;
      width: 100%;
    }
    &__items{
      padding: 0;
      border: none;
      gap: 10px;
      grid-template-columns: repeat(2,calc(50% - 5px));
    }
    &__item{
      border: 1px solid #DBD7E9;
      border-radius: 10px;
      height: 44px;
      padding: 0 10px;
      justify-content: center;
      &._selected{
        border-color: $primary;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__more{
      width: 100%;
    }
  }
}
