.order{
  padding-bottom: 50px;
  &__tabs{
    margin-top: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    background: linear-gradient(90deg, #F6F6FA 0%, #FFFFFF 103.33%);
    border: 1px solid #F2F1F6;
    border-radius: 10px;
    overflow: hidden;

  }
  &__tab{
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    background-repeat: no-repeat;
    cursor: pointer;
    transition: all .3s;
    &-icon{
      background: #F2F1F6;
      width: 40px;
      height: 40px;
      margin-right: 10px;
      flex-shrink: 0;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      svg{
        fill:#DBD7E9;
      }
    }
    &:hover{
      opacity: .8;
    }
    &:nth-child(1){
      background-image: url(../img/tab-bg.svg);
      background-position: right center;
      position: relative;
      z-index: 2;
      color: #fff;
      .order__tab-icon{
        background: #AE7BFF;
        svg{
          fill: #fff;
        }
      }
    }
    &:nth-child(2){
      width: calc(100% + 40px);
      margin-left: -40px;
      background-size: cover;
      background-position: center;
    }
  }
  &._show-pay{
    .order__tabs{
      background: linear-gradient(90deg, #434DFE 0%, #FF9DFF 100%);
    }
    .order__tab{
      color: #fff;
      &:nth-child(2){
        background-image: url(../img/tab-bg2.svg);
      }
      &-icon{
        background: #AE7BFF;
        svg{
          fill: #fff;
        }
      }
    }
    .cart{
      display: none;
    }
    .pay{
      display: block;
    }
  }
  &__nav{
    margin-top: 20px;
    padding-top: 20px;
    border-top: 1px dashed #DBD7E9;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__back,
  &__pay{
    width: 180px;
    svg{
      margin-right: 10px;
    }
  }
}

.cart{
  margin-top: 10px;
  &__head{
    display: grid;
    grid-template-columns: auto 100px 162px 110px 100px;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.01em;
    @include fontWeight(350,$font2);
  }
  &__items{
    margin-top: 10px;
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
  }
  &__form{
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__promo{
    width: 380px;
    position: relative;
    input{
      padding-right: 200px;
    }
    &-btn{
      position: absolute;
      top: 0;
      right: 0;
      width: 180px;
    }
  }
  &__check{
    label{
      font-size: 16px;
    }
  }
  &__form2{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 10px;
  }
  &__comment{
    width: 570px;
    input{
      padding: 0 20px;
    }
  }
  &__sum{
    &-num{
      font-weight: 600;
      &:after{
        content: attr(data-currency);
      }
    }
  }
}

.pay{
  display: none;
  margin-top: 20px;
  &__wrap{
    background: #FFFFFF;
    box-shadow: 0px 5px 40px #F2F1F6;
    border-radius: 30px;
  }
  &__group{
    padding: 20px 30px;
    &:not(:last-child){
      border-bottom: 1px dashed #DBD7E9;
    }
  }
  &__desc{
    margin-top: 10px;
    p{
      margin: 0 !important;
    }
  }
  &__email{
    margin-top: 20px;
    max-width: 340px;
  }
  &__methods{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 2px;
    background: #F2F1F6;
    border-radius: 10px;
    padding: 5px;
    margin-top: 20px;
  }
  .brand-filter__item{
    label{
      justify-content: center;
      svg{
        width: 24px;
      }
    }
  }
  &__detail{
    display: flex;
    justify-content: space-between;
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
  }
  &__item{
    &-title{
      font-weight: 600;
    }
  }
  .order__nav{
    padding-top: 0;
    border: none;
  }
}

.orders{
  padding-bottom: 50px;
  &__grid{
    display: grid;
    grid-template-columns: 100%;
    gap: 42px;
    margin-top: 42px;
  }
}


@media screen and (max-width: 992px){
  .cart{
    &__head{
      grid-template-columns: auto 80px 132px 90px 100px;
    }
  }

  .pay{
    &__wrap{
      border-radius: 20px;
    }
    &__group{
      padding: 10px 15px;
    }
  }
}


@media screen and (max-width: 768px){
  .order{
    &__tab{
      height: 50px;
      font-size: 16px;
      &-icon{
        width: 35px;
        height: 35px;
      }
    }
    &__nav{
      display: grid;
      grid-template-columns: 100%;
      gap: 10px;
    }
    &__back,
    &__pay{
      width: 100%;
    }
  }

  .cart{
    &__head{
      display: none;
    }
    &__items{
      margin-top: 30px;
    }
    &__form,
    &__form2{
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
    }
    &__promo,
    &__comment{
      width: 100%;
    }
    &__promo{
      input{
        padding-right: 150px;
      }
      &-btn{
        width: 140px;
      }
    }
  }

  .pay{
    &__methods{
      background-color: transparent;
      padding: 0;
      grid-template-columns: 100%;
      gap: 5px;
    }
    &__detail{
      display: block;
    }
    &__sum{
      margin-top: 30px;
    }
  }
}
