.reviews{
  padding-bottom: 50px;
  .feedback-card{
    background: #FFFFFF;
    box-shadow: 0px 5px 40px #F2F1F6;
    border-radius: 20px;
    padding: 20px 30px;
    &:not(:last-child){
      margin-bottom: 10px;
    }
    &__answer{
      width: calc(100% - 40px);
    }
  }
  &__items{
    margin-top: 20px;
  }
  &__more{
    height: 60px;
    margin: 20px auto 0;
    width: 260px;
    svg{
      margin-left: 10px;
      fill: $primary;
    }
  }
}

.faq-page{
  padding-bottom: 50px;
  &__items{
    margin-top: 20px;
  }
  .faq-card{
    background: #FFFFFF;
    box-shadow: 0px 5px 40px #F2F1F6;
    border-radius: 20px;
    padding: 20px 30px;
  }
}


@media screen and (max-width: 768px){
  .reviews{
    .feedback-card{
      padding: 15px 20px;
      border-radius: 20px;
      &__answer{
        width: 100%;
      }
    }
    &__more{
      width: 100%;
    }
  }

  .faq-page{
    .faq-card{
      border-radius: 20px;
      padding: 15px 20px;
    }
  }
}
