.promos{
  padding-bottom: 50px;
  &__head{
    display: grid;
    grid-template-columns: auto 170px 220px 218px;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.01em;
    @include fontWeight(350,$font2);
    padding: 0 20px;
    margin-top: 20px;
    text-align: center;
  }
}


.promo-card{
  margin-top: 10px;
  padding: 23px 20px;
  display: grid;
  grid-template-columns: auto 170px 220px 218px;
  align-items: center;
  background: #FFFFFF;
  border-radius: 20px;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    left: 0;
    top: 50%;
    transform: translate(-50%,-50%);
    background: #f6f6fa;
  }
  &__title{
    padding: 0 10px 0 35px;
  }
  &__code{
    border: 1px dashed #DBD7E9;
    border-radius: 5px;
    background: #F2F1F6;
    height: 54px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 20px;
    svg{
      margin-right: 10px;
    }
  }
  &__date{
    text-align: center;
  }
  &__btn{
    width: 100%;
  }
  &__subtitle{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .promos{
    &__head{
      grid-template-columns: auto 160px 140px 180px;
    }
  }

  .promo-card{
    grid-template-columns: auto 160px 140px 180px;
    &__title{
      padding: 0 20px 0 10px;
    }
  }
}


@media screen and (max-width: 768px){
  .promos{
    &__head{
      display: none;
    }
  }

  .promo-card{
    grid-template-columns: 100%;
    gap: 20px;
    text-align: center;
    padding: 20px;
    &__title{
      padding: 0;
    }
    &__date{
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__subtitle{
      display: block;
      margin-right: 10px;
    }
  }
}
