@import "../vars.scss";
.breadcrumbs {
    margin-top: 20px;
    &__item {
        font-size: 12px;
        line-height: 150%;
        position: relative;
        font-weight: 400;
        display: inline;
        color: #8C84AA;
        &:not(:first-child) {
            &:before {
                color: #8C84AA;
                content: '/';
                margin: 0 5px 0 1px;
            }
        }
        &:last-child{
            color: $primary;
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        margin-bottom: 10px;
    }
}
