@import "../mixins";

.logo{
  display: block;
  width: 129px;
  flex-shrink: 0;
  svg{
    width: 100%;
    height: auto;
    display: block;
  }
}


.header{
  padding: 20px 0;
  background-color: #fff;
  position: relative;
  z-index: 3;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    margin-right: 50px;
  }
  &__right{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;
    max-width: 892px;
  }
  &__support{
    display: flex;
    align-items: center;
    &-title{
      font-size: 14px;
      line-height: 130%;
      margin-right: 10px;
    }
    &-link{
      margin-left: 10px;
      &:hover{
        svg{
          fill: $primary;
        }
      }
    }
  }
  &__buttons{
    display: flex;
    align-items: center;
  }
  &__user{
    margin-right: 20px;
  }
  &__user,
  &__cart{
    position: relative;
    &-btn{
      &:hover{
        svg{
          fill: $primary;
        }
      }
    }
    &._active{
      .m-dropdown{
        opacity: 1;
        pointer-events: unset;
        &__inner{
          transform: translateY(0);
        }
      }
    }
  }
  &__cart{
    &-count{
      position: absolute;
      top: -14px;
      right: -13px;
      border-radius: 50px;
      background-color: $primary;
      color: #fff;
      padding: 0 5px;
      font-weight: 600;
      font-size: 12px;
      line-height: 17px;
      height: 23.44px;
      display: flex;
      align-items: center;
    }
  }
}

.burger{
  display: none;
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 18px;
    line-height: 26px;
    &:not(:last-child){
      margin-right: 40px;
    }
  }
}

.auth-modal{
  width: 280px;
  max-width: 100vw;
  &__mode{
    display: none;
    &._active{
      display: block;
    }
  }
  &__title{
    font-size: 20px;
    line-height: 150%;
  }
  &__desc{
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.01em;
    margin-top: 5px;
    @include fontWeight(350,$font2);
  }
  &__form{
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 10px;
  }
  &__toggle{
      color: $primary;
    &:hover{
      text-decoration: underline;
    }
  }
}

.cart-modal{
  width: 350px;
  max-width: 100vw;
  &__items{
    max-height: 395px;
    overflow-y: auto;
  }
  &__total{
    margin-top: 10px;
    text-align: center;
    font-size: 20px;
    line-height: 150%;
    letter-spacing: 0.01em;
    @include fontWeight(350,$font2);
    b{
      font-weight: 600;
    }
  }
  &__btn{
    width: 100%;
    margin-top: 10px;
  }
}


@media screen and (max-width: 992px){
  .header{
    position: relative;
    &__logo{
      margin-right: 0;
    }
    &__right{
      max-width: unset;
      flex-grow: unset;
    }
    &__support{
      display: none;
    }
    &__user{

    }
    &__cart,
    &__user{
      margin-right: 10px;
      position: static;
      &-btn{
        border: 1px solid #434DFE;
        border-radius: 10px;
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        svg{
          width: 18px;
          height: 18px;
        }
      }
      .m-dropdown{
        right: 15px;
        left: unset;
        transform: unset;
        &__triangle-wrap{
          display: none;
        }
      }
    }
    &__cart{
      &-count{
        top: -5px;
        right: -5px;
      }
    }
    &._open{
      z-index: 100;
      .nav{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        background-image: url(../img/burger2.svg);
      }
    }
  }

  .nav{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(68, 64, 97, 0.95);
    backdrop-filter: blur(10px);
    flex-direction: column;
    justify-content: center;
    overflow-y: auto;
    z-index: 9;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__link{
      font-size: 32px;
      line-height: 46px;
      color: #fff;
      &:not(:last-child){
        margin: 0 0 30px;
      }
    }
  }

  .burger{
    width: 40px;
    height: 40px;
    border-radius: 10px;
    background-color: $primary;
    display: flex;
    background-image: url(../img/burger.svg);
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
    z-index: 10;
  }
}


@media screen and (max-width: 576px){
  .auth-modal,
  .cart-modal{
    width: calc(100% - 30px);
  }
}
