.catalog-item{
  position: relative;
  z-index: 2;
  &__wrap{
    margin-top: 20px;
    display: grid;
    grid-template-columns: auto 370px;
    gap: 30px;
    padding: 60px 0 60px 60px;
    position: relative;
  }
  &__bg{
    position: absolute;
    background-color: #fff;
    border-radius: 30px;
    width: calc(100% - 100px);
    height: 100%;
    z-index: -1;
    @media screen and (min-width: 992px){
      &:before{
        width: calc(100% - 20px);
        height: calc(100% - 20px);
      }
    }
  }
  &__content{
    max-width: 625px;
  }
  &__text{
    p{
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
    &-title{
      font-weight: 600;
      padding-left: 15px;
      &:not(:last-child){
        margin-bottom: 10px !important;
      }
    }
    &-item{
      @include fontWeight(350,$font2);
      display: flex;
      align-items: center;
      &:before{
        content: '';
        display: block;
        width: 5px;
        height: 5px;
        flex-shrink: 0;
        border-radius: 50%;
        margin-right: 10px;
        background-color: $primary;
      }
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
  }
  &__nav{
    margin-top: 30px;
    display: flex;
    align-items: center;
  }
  &__price{
    margin-right: 21px;
    display: flex;
    align-items: center;
    &-old{
      font-size: 16px;
      line-height: 23px;
      letter-spacing: 0.01em;
      text-decoration-line: line-through;
      color: #7B739E;
      margin-right: 12px;
    }
    &-new{
      font-weight: 500;
      font-size: 18px;
      line-height: 26px;
      letter-spacing: 0.01em;
    }
  }
  &__btn{
    width: 139px;
    height: 50px;
    svg{
      margin-right: 10px;
    }
  }
  &__card{
    .product-card__top{
      margin: 0;
    }
  }
}

.gift{
  padding: 234px 0 50px;
  margin-top: -204px;
  background-image:url(../img/gift-bg.webp), radial-gradient(80.25% 171.06% at 32.76% 27.25%, #0B3A71 0%, #2568B7 100%);
  background-position: center;
  background-color: #D9D9D9;
  background-size: cover;
  &__title{
    color: #fff;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 24px));
    gap: 50px 30px;
    margin-top: 40px;
  }
}

.catalog-detail{
  padding: 20px 0 30px;
  margin-top: 20px;
  margin-bottom: 20px;
  background: #FFFFFF;
  box-shadow: 0px 5px 40px #F2F1F6;
  &__tabs{
    padding: 5px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 2px;
    background: #F2F1F6;
    border-radius: 10px;
  }
  &__tab{
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: inherit;
    border-radius: 10px;
    transition: all .3s;
    cursor: pointer;
    &:hover,
    &._active{
      background-color: $primary;
      color: #fff;
    }
    &._active{
      pointer-events: none;
    }
  }
  .tab{
    display: none;
    &._active{
      display: block;
    }
  }
  &__content{
    margin-top: 20px;
  }
  &__mob{
    &-toggle{
      display: none;
    }
  }
}

.rules{
  &__group{
    &:not(:last-child){
      margin-bottom: 20px;
      padding-bottom: 20px;
      border-bottom: 1px dashed #DBD7E9;
    }
  }
  &__title{
    max-width: 775px;
  }
  &__list{
    margin-top: 20px;
    @include fontWeight(350,$font2);
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    max-width: 770px;
    counter-reset: list;
    li{
      line-height: inherit;
      &:before{
        counter-increment: list;
        content: counter(list)". ";
      }
    }
  }
}


@media screen and (max-width: 1150px){
  .gift{
    &__grid{
      grid-template-columns: repeat(4,calc(25% - 22.5px));
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    &__wrap{
      border-radius: 20px;
      padding: 20px;
      grid-template-columns: 100%;
    }
    &__bg{
      width: 100%;
    }
    &__card{
      order: -1;
      max-width: 320px;
    }
  }

  .gift{
    margin-top: 20px;
    padding: 30px 0;
    &__grid{
      grid-template-columns: repeat(3,calc(33.33% - 20px));
    }
  }
}


@media screen and (max-width: 768px){
  .gift{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 15px));
    }
  }

  .catalog-detail{
    padding: 0;
    max-width: 100%;
    background-color: transparent;
    box-shadow: none;
    &__tabs{
      display: none;
    }
    &__content{
      margin: 0;
    }
    .tab{
      padding: 20px 15px;
      background: #FFFFFF;
      box-shadow: 0 5px 40px #F2F1F6;
      border-radius: 20px;
      display: block;
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
    &__mob{
      &-toggle{
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 20px;
        &._active{
          svg{
            transform: rotate(180deg);
          }
        }
        svg{
          transition: all .4s;
        }
      }
      &-content{
        display: none;
        margin-top: 25px;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    &__nav{
      flex-direction: column;
    }
    &__price{
      margin: 0 0 10px;
    }
    &__btn{
      width: 100%;
    }
    &__text{
      p,
      &-item{
        font-size: 14px;
        align-items: unset;
        &:not(:last-child){
          margin-bottom: 10px;
        }
        &:before{
          transform: translateY(10px);
        }
      }
    }
  }

  .gift{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
